
import { GridItem } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  name: "PanelCreateItem",

  data: () => ({
    types: [],
    item: {
      x: 0,
      y: 0,
      w: 0,
      h: 0,
      i: 0,
      data: {
        active: true,
        name: "",
        type: "pomodoro",
      },
    } as GridItem,
  }),

  methods: {
    handleForm() {
      const payload = { ...this.item };

      this.$emit("addItem", payload);

      this.$emit("closePanel");
    },
  },
});
